.contents {
    margin-top: 20px;
}

.image-container {
    width: 65%;
    display: flex;
    justify-content: center;
    height: 100px;
    margin: auto;

    img {
        margin: auto;
    }
}

.card {
    border: solid grey 2px;
    border-radius: 10px;
    width: 250px;
    height: 225px;
    background-color: #f0f0f0;
    padding: 15px;
    z-index: 1;

    img {
        width: auto;
        height: 75px;
    }

    h1 {
        text-align: center;
        font-size: small;
    }

    &:hover {
        color: white;
        background-color: #a9a9a9;
        transform: scale(1.05);
        transition: 1s;
    }
}
